import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './Header.scss';

import GridHelper from './GridHelper';

class Header extends Component {
  constructor() {
    super();
    this.state = {
      height: 'auto'
    }
  }
  render() {
    return (
      <div className='Header' style={{ height: this.state.height }}>
        <GridHelper size='wide' span={10} ref='header'>
          <h1>
            Jure Martinec is a European designer and technologist, researching how digital might be shaping the world, often through code and interactive artifacts. He works at <a href='https://www.ideo.com/' target='_blank'>IDEO</a> and is with <a href='http://www.ansambel.org/' target='_blank'>Ansambel</a>. <a href='mailto:hello@jure.io'>Get in touch</a> and follow on <a href='https://twitter.com/youremartinets' target='_blank'>Twitter</a>, <a href='https://linkedin.com/in/juremartinec' target='_blank'>LinkedIn</a> or <a href='https://github.com/murej' target='_blank'>GitHub</a>.
          </h1>
        </GridHelper>
      </div>
    );
  }
  componentDidMount() {
    this.setState({
      height: ReactDOM.findDOMNode(this.refs.header).clientHeight + 'px'
    });
  }
}

export default Header;



