import React from 'react';
import ReactDOM from 'react-dom';
import { Router, IndexRoute, Route, hashHistory, applyRouterMiddleware } from 'react-router';
import { useScroll } from 'react-router-scroll';
import ReactGA from 'react-ga';
import App from './App';

import ProjectIndexPage from './components/ProjectIndexPage';
import ProjectPage from './components/ProjectPage';

import './assets/fonts/fonts.css';
import './index.scss';

ReactGA.initialize('UA-25602894-1');

function logPageView() {
  ReactGA.set({ page: window.location.pathname });
  ReactGA.pageview(window.location.pathname);
}

useScroll((prevRouterProps, { location }) => (
  //prevRouterProps && location.pathname !== prevRouterProps.location.pathname
  [0, 0]
));

ReactDOM.render(
  <Router history={hashHistory} render={applyRouterMiddleware(useScroll())} onUpdate={logPageView}>
    <Route path="/" component={App}>
      <IndexRoute component={ProjectIndexPage} />
      <Route path=":project" component={ProjectPage} />
    </Route>
  </Router>,
  document.getElementById('root')
);



