import React, { Component } from 'react';
import { Link } from 'react-router';
import isEmpty from 'lodash/isEmpty';
import cx from 'classnames';
import './ProjectHeader.scss';

class ProjectHeader extends Component {
  constructor() {
    super();

    this.state = {
      isHovered: false,
      highlightStyle: null,
      confidentialStyle: null,
      imageStyle: null
    }
  }
  componentWillMount() {
    const highlightStyle = {
      top: this._getRandomArbitrary(10, 20)+'%',
      transform: 'translateX('+this._getRandomArbitrary(-20, 10)+'%) rotate('+this._getRandomArbitrary(-4, 1)+'deg)',
    }
    // const confidentialStyle = {
    //   width: this._getRandomArbitrary(105, 130)+'%',
    //   transform: 'translateX('+this._getRandomArbitrary(-15, -5)+'%) rotate('+this._getRandomArbitrary(-1.5, 0.5)+'deg)',
    // }
    const imageStyle = {
      transform: `translateX(${this._getRandomArbitrary(-10, 25)}%) translateY(${this._getRandomArbitrary(-50, -10)}%) rotate(${this._getRandomArbitrary(-4, 4)}deg)`,
      backgroundColor: this.props.backgroundColor,
      // backgroundBlendMode: this.props.blendMode
    }

    this.setState({
      highlightStyle: highlightStyle,
      // confidentialStyle: confidentialStyle,
      imageStyle: imageStyle
    })
  }

  render() {
    const isConfidential = this.props.isConfidential;
    const title = isConfidential ? '(non-public) '+this.props.title : this.props.title;
    const image = this.props.image;
    const hasImage = !isEmpty(image);
    const hasLink = !isEmpty(this.props.link);
    const className = cx({
      'ProjectHeader': true,
      'ProjectHeader-Confidential': isConfidential
    });
    const imageClassName = cx({
      'ProjectHeader-Image': true,
      'ProjectHeader-Image--show': this.state.isHovered
    });

    let highlightStyle = Object.assign({}, this.state.highlightStyle);
    highlightStyle.backgroundSize = this.state.isHovered ? this._getRandomArbitrary(100, 120)+'% 70%' : '100% 10%';

    let imageStyle = Object.assign({}, this.state.imageStyle);
    // hasImage && (imageStyle.backgroundImage = `url(${image})`);

    return (
      <div className={className}>
        {hasLink && <Link to={this.props.link.to} onMouseEnter={this._handleOnMouseEnter} onMouseLeave={this._handleOnMouseLeave}></Link>}
        <h2>{title}<span style={ hasLink ? highlightStyle : {} }></span></h2>
        <p><span>{this.props.caption}</span></p>
        {hasImage && <div className={imageClassName} style={imageStyle}><img src={image} /></div>}
      </div>
    );
  }
  _handleOnMouseEnter = () => {
    this.setState({ isHovered: true });
  }
  _handleOnMouseLeave = () => {
    this.setState({ isHovered: false });
  }

  _getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
  }
}

ProjectHeader.propTypes = {
  link: React.PropTypes.object,
  title: React.PropTypes.string.isRequired,
  caption: React.PropTypes.string,
  isConfidential: React.PropTypes.bool
};
ProjectHeader.defaultProps = {};

export default ProjectHeader;



